<template lang="pug">

.list
	.list-nav
		input.list-nav-input(placeholder="Поиск" v-model="search")
		.list-nav-info {{ $store.state.catalog.server.sum }} Р / {{ prods.length }} наим
		.list-nav-sort
			.list-nav-sort-item популярность
			.list-nav-sort-item алфавит
			.list-nav-sort-item цена
	.list-info
		.list-info-name Название, Производитель, Вес, Кол-во в упаковке
		.list-info-price Цена
		.list-info-calc Калькулятор
	v-card(v-for="prod in prods" :prod="prod" :key="prod.id")

</template>

<script>
import iCard from 'temp/prods/card.vue'

export default {
	data(){
		return {
			searchStr: ''
		}
	},
	computed: {
		search: {
			get(){
				return this.searchStr
			},
			set(val){
				this.searchStr = val
				setTimeout(()=>{
					if (this.searchStr != val) return
					this.$store.state.catalog.search = val
					this.$store.dispatch('loadProds')
				}, 700)
			}
		},
		prods: {
			get(){
				return this.$store.state.catalog.prods.cart
			}
		}
	},
	components: {
		'v-card': iCard
	}
}

</script>

<style lang="stylus" scoped>

.list
	&-nav
		background-color #fff
		box-shadow 0 0 6px 0 rgba(#000, .1)
		margin-bottom 15px
		padding 0 13px
		height 50px
		border-radius 10px
		display flex
		justify-content space-between
		align-items center
		&-input
			font-size 14px
			background-color #f6f6f6
			border 0
			border-radius 6px
			padding 5px 13px
			width 25%
			&::placeholder
				color #888
		&-info
			font-size 14px
		&-sort
			display flex
			margin-right 5px
			&-item
				font-size 14px
				margin-left 15px
				cursor pointer
				line-height 1
	&-info
		font-size 14px
		display flex
		margin -5px 0 10px
		padding 0 15px
		color #888
		&-name
			width 50%
		&-price
			width 20%
		&-calc
			width 30%
			text-align right
			display none

</style>
